.header {
    padding: 20px 0;
    border-bottom: 2px solid #D6D7DD;
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav__list {
    display: flex;
    align-items: center;
}

.nav__item:not(:last-child) {
    margin-right: 20px;
}

.header__logo--text {
    margin: 0;
    font-size: 18px;
    font-weight: 700;
    text-align: left;
    text-transform: uppercase;
    user-select: none;
}

.header__logo {
    position: relative;
}

.header__logo::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #FEE400;
    bottom: 8px;
    z-index: -1;
}