.nav__link {
    color: #1E1613;
    font-size: 16px;
    font-weight: 400;
    position: relative;
}

.nav__link::after {
    position: absolute;
    display: block;
    content: '';
    width: 0;
    height: 1px;
    background-color: #1E1613;
    transition: width .3s ease-in-out;
}

.nav__link.active.nav__link::after {
    width: 100%;
}

@media(hover:hover) {
    .nav__link:hover.nav__link::after {
        width: 100%;
    }
}